//  name
import msSelfAside from './src/ms-self-aside'
import mixins from "../../src/mixins/default";

if (typeof window !== 'undefined' && window.Vue) {
    window.Vue.mixin(mixins)
    window.Vue.component(msSelfAside.name, msSelfAside)
}

//  install 
msSelfAside.install = function (Vue) {
    Vue.component(msSelfAside.name, msSelfAside)
}

// 
export default msSelfAside
